import * as React from "react";
import MainLayout from "../components/layouts/MainLayout";
import ImgCode1 from "../images/imgprojects/pexels-negative-space-97077.jpg";
import ImgCode2 from "../images/imgprojects/pexels-negative-space-97077v2.jpg";

const features = [
  {
    name: "Origin",
    description:
      "Everything started when smart phones took advantage over computers in  web surfing.",
  },
  {
    name: "Material",
    description:
      "JAMstack infrastructure, provide maximum perforant web presentations. Now creativity is the only limit.",
  },
  {
    name: "Dimensions",
    description:
      "Ultra small package every internet connection will fully loaded with ease.",
  },
  { name: "Finish", description: "Super fast and responsive web aplications." },
  {
    name: "Includes",
    description:
      "Last visited version is always available when you are offline.",
  },
  {
    name: "Considerations",
    description:
      "Green agenda. No need for servers as everything is distributed worldwide trough Content Devlivery Networks.",
  },
];

export default function ServicesPage() {
  return (
    <MainLayout>
      <div className="">
        <div className="max-w-4xl mx-auto pt-12 pb-24 px-4 grid items-center grid-cols-1 lg:max-w-7xl lg:px-8">
          <div className="">
            <h2 className="text-3xl tracking-tight text-gray-50 sm:text-5xl text-center">
              Our Services
            </h2>
            <h3 className="mt-4 text-gray-50 text-center text-lg sm:text-2xl font-light">
              Responisve, Performant, Cheap. Choose two!
            </h3>
          </div>
        </div>
        <div className="max-w-4xl mx-auto px-4 grid items-center grid-cols-1 gap-y-16 gap-x-8 sm:px-6 lg:max-w-7xl lg:px-8 lg:grid-cols-2">
          <div>
            <div className="grid sm:grid-cols-2 grid-rows-2 gap-4 sm:gap-6 lg:gap-8">
              <img
                src={ImgCode1}
                // https://www.gatsbyjs.com/docs/recipes/working-with-images/
                alt="Walnut card tray with white powder coated steel divider and 3 punchout holes."
                className="bg-gray-100 rounded-lg rotate-180 sm:mix-blend-luminosity hover:mix-blend-normal hover:scale-110"
              />
              <img
                src={ImgCode2}
                alt="Top down view of walnut card tray with embedded magnets and card groove."
                className="bg-gray-100 rounded-lg rotate-180 sm:mix-blend-luminosity hover:mix-blend-normal hover:scale-110"
              />
              <img
                src={ImgCode2}
                alt="Side of walnut card tray with card groove and recessed card area."
                className="bg-gray-100 rounded-lg sm:mix-blend-luminosity hover:mix-blend-normal hover:scale-110"
              />
              <img
                src={ImgCode1}
                alt="Walnut card tray filled with cards and card angled in dedicated groove."
                className="bg-gray-100 rounded-lg sm:mix-blend-luminosity hover:mix-blend-normal hover:scale-110"
              />
            </div>
          </div>
          <div className="">
            <dl className="pb-24 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 sm:gap-y-8 lg:gap-x-8 text-center ">
              {features.map((feature) => (
                <div
                  key={feature.name}
                  className="border-t border-gray-200 pt-4 "
                >
                  <dt className="font-medium text-gray-50">{feature.name}</dt>
                  <dd className="mt-2 text-sm text-gray-200">
                    {feature.description}
                  </dd>
                </div>
              ))}
            </dl>
          </div>
        </div>
      </div>
    </MainLayout>
  );
}
