import React from "react";
import { useState } from "react";
import { Link } from "gatsby";
import logo from "../../images/header/ddlogo.svg";

export default function Hamburger() {
  const [isNavOpen, setIsNavOpen] = useState(false);

  return (
    <div className="sticky top-0 z-10 flex items-center justify-between bg-delirium-dark p-4 uppercase text-gray-50 md:justify-center">
      <Link to="/">
        <img
          src={logo}
          alt="logo"
          className="h-10 pl-4 mix-blend-plus-lighter md:hidden"
        />
      </Link>
      <nav>
        <section className="MOBILE-MENU flex md:hidden">
          <div
            className="HAMBURGER-ICON space-y-2 pr-4"
            onClick={() => setIsNavOpen((prev) => !prev)}
            onKeyDown={() => setIsNavOpen((prev) => !prev)}
            role="button"
            tabIndex={0}
          >
            <span className="ml-1 block h-0.5 w-7 animate-pulse bg-gray-50"></span>
            <span className="block h-0.5 w-8 animate-pulse bg-gray-50"></span>
            <span className="ml-1 block h-0.5 w-7 animate-pulse bg-gray-50"></span>
          </div>

          <div className={isNavOpen ? "showMenuNav" : "hideMenuNav"}>
            <div
              className="absolute top-0 right-0 pt-4 pr-6"
              onClick={() => setIsNavOpen(false)}
              onKeyDown={() => setIsNavOpen(false)}
              role="button"
              tabIndex={0}
            >
              <svg
                className="h-11 w-11 text-gray-50"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="1"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18" />
                <line x1="6" y1="6" x2="18" y2="18" />
              </svg>
            </div>
            <ul className="flex min-h-[200px] flex-col items-center justify-between text-2xl">
              <li className="my-8 border-b border-gray-400 uppercase">
                <Link to="/services">Our Services</Link>
              </li>
              <li className="my-8 border-b border-gray-400 uppercase">
                <Link to="/projects">Our Work</Link>
              </li>
              <li className="my-8 border-b border-gray-400 uppercase">
                <Link to="/about">About Us</Link>
              </li>
              <li className="my-8 animate-pulse border-b border-gray-400 uppercase">
                <Link to="/contact">Contact Us</Link>
              </li>
            </ul>
          </div>
        </section>

        <ul className="DESKTOP-MENU hidden items-center justify-between md:flex ">
          <li>
            <Link
              className="p-4 px-8 hover:bg-indigo-700 hover:overline"
              to="/"
            >
              Home
            </Link>
          </li>
          <li>
            <Link
              className="from-indigo-700 to-indigo-600 p-4 hover:bg-gradient-to-r hover:overline"
              to="/services"
            >
              Our Services
            </Link>
          </li>
          <li>
            <Link
              className="p-4 px-6 hover:bg-indigo-600 hover:overline"
              to="/projects"
            >
              Our Work
            </Link>
          </li>
          <li>
            <Link
              className="from-indigo-600 to-indigo-700 p-4 hover:bg-gradient-to-r hover:overline"
              to="/about"
            >
              About Us
            </Link>
          </li>
          <li>
            <Link
              className="animate-pulse from-indigo-700 to-indigo-800 p-4 hover:animate-none hover:bg-gradient-to-r hover:overline"
              to="/contact"
            >
              Contact Us
            </Link>
          </li>
        </ul>
      </nav>
      <style>{`
      .hideMenuNav {
        display: none;
      }
      .showMenuNav {
        display: block;
        position: absolute;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background: #26292E;
        z-index: 10;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
      }
    `}</style>
    </div>
  );
}
